import React from 'react';
import styled from 'styled-components';
import { Input } from 'components/Input/Input';
import { useSelector, useDispatch } from 'hooks';
import { addressChangeInputValue, addressSetSuggests, addressSubmit } from 'store/actions';
import { Header } from 'components/Header/Header';
import { getSuggests } from 'tools/2gis';
import { Button } from 'components/Button/Button';

import {
    AddressSimpleSuggestListItem,
    SUGGEST_ITEM_HEIGHT,
    SUGGEST_ITEM_MARGIN,
} from './SuggestItem/AddressSimpleSuggestItem';

const StyledContainer = styled.div`
    display: flex;
    align-items: start;
    height: 100%;
    padding: 160px 0 0 340px;
`;

const StyledInputContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 0 0;
`;

const AddressSimpleButton = styled(Button)`
    margin-left: 32px;
    padding: 32px 48px;
    background: rgba(255, 255, 255, 0.12);
    border-radius: 140px;
    font-weight: 600;
    font-size: 40px;
    line-height: 48px;
    color: #ffffff;
`;

interface StyledSuggestListProps {
    hidden?: boolean;
}
const StyledSuggestList = styled.div<StyledSuggestListProps>`
    height: ${({ hidden }) => (hidden ? 0 : (SUGGEST_ITEM_HEIGHT + SUGGEST_ITEM_MARGIN) * 3)}px;
    overflow: hidden;
    transition: height 0.5s ease-in-out;
`;

interface StyledSuggestListContainerProps {
    YShift: number;
}
const StyledSuggestListContainer = styled.div<StyledSuggestListContainerProps>`
    display: flex;
    flex-direction: column;

    transition: transform 0.5s ease-in-out;
    transform: translateY(-${({ YShift }) => YShift}px);
`;

const preventScroll = (event: React.SyntheticEvent<HTMLDivElement>) => {
    event.currentTarget.scrollTop = 0;
};

export const AddressSimple: React.FC = () => {
    const submitButtonRef = React.useRef<HTMLButtonElement>(null);
    const [isKeyboardExpanded, setIsKeyboardExpanded] = React.useState(false);

    const dispatch = useDispatch();
    const { inputValue, suggests, suggestListContainerYShift, isSubmitButtonDisabled, deviceLocation } = useSelector(
        ({ addressSimplePage }) => addressSimplePage,
    );

    React.useEffect(() => {
        const onResize = () => {
            setIsKeyboardExpanded(!isKeyboardExpanded);
        };

        window.addEventListener('resize', onResize);

        return () => {
            window.removeEventListener('resize', onResize);
        };
    }, [isKeyboardExpanded, setIsKeyboardExpanded]);

    const memoizedOnInputChange = React.useCallback<(e: React.ChangeEvent<HTMLInputElement>) => void>(
        (event) => {
            dispatch(addressChangeInputValue(event.currentTarget.value));
        },
        [dispatch],
    );
    const memoizedOnInputSubmit = React.useCallback(() => {
        if (submitButtonRef && submitButtonRef.current instanceof HTMLButtonElement) {
            submitButtonRef.current.focus();
        }
    }, []);
    const memoizedOnClickButton = React.useCallback(() => {
        dispatch(addressSubmit());
    }, [dispatch]);

    React.useEffect(() => {
        getSuggests({ q: inputValue, location: deviceLocation })
            .then((items) => {
                dispatch(addressSetSuggests(items));
            })
            .catch(() => {
                dispatch(addressSetSuggests([]));
            });
    }, [dispatch, inputValue, deviceLocation]);

    return (
        <>
            <Header />
            <StyledContainer>
                <StyledInputContainer>
                    <Input
                        shouldFocusOnMount
                        value={inputValue}
                        title="Введите дом и улицу"
                        onChange={memoizedOnInputChange}
                        onSubmit={memoizedOnInputSubmit}
                    />
                    <StyledSuggestList hidden={isKeyboardExpanded} onScroll={preventScroll}>
                        <StyledSuggestListContainer YShift={suggestListContainerYShift}>
                            {suggests.map((item, index) => (
                                <AddressSimpleSuggestListItem key={item.id} index={index} />
                            ))}
                        </StyledSuggestListContainer>
                    </StyledSuggestList>
                </StyledInputContainer>
                <AddressSimpleButton
                    disabled={isSubmitButtonDisabled}
                    forwardRef={submitButtonRef}
                    onClick={memoizedOnClickButton}
                >
                    Сохранить
                </AddressSimpleButton>
            </StyledContainer>
        </>
    );
};
